<template>
    <div class="row">
      <h3 v-if="details.title != undefined">{{ details.title[$i18n.locale] }}</h3>
      <span v-html="listGen(details.items)"></span>
    </div>
</template>

<script>
export default {
  name: 'List',
  props: {
    details: Object,
  },
  methods: {
    listGen(items) {
      let output = '<ul>';
      for (let i = 0; i < items.length; i += 1) {
        if (Array.isArray(items[i])) {
          output += this.listGen(items[i]);
        } else {
          output += `<li>${items[i][this.$i18n.locale]}</li>`;
        }
      }
      output += '</ul>';
      return output;
    },
  },
};
</script>
