<template>
  <div class="container py-4">
    <div class="p-5 mb-4 bg-light rounded-3">
      <h1>{{ info.name[$i18n.locale] }}</h1>
      <Group :details="details" />
      <BackButton v-if="back_allow" :url="info.url" />
    </div>
  </div>
</template>

<script>
import BackButton from '@/components/details/BackButton.vue';
import Group from '@/components/details/Group.vue';

export default {
  name: 'ApplicationDetails',
  props: {
    id: String,
    page: String,
    back_allow: {
      type: Boolean,
      default: true,
    },
  },
  components: {
    BackButton,
    Group,
  },
  data() {
    return {
      info: {},
      details: null,
    };
  },
  methods: {
    queryData(page, setData) {
      fetch(`/json/application/${this.id}/${page}.json`)
        .then((r) => r.json())
        .catch(() => {
          this.$router.push('/error/404');
        })
        .then((json) => { setData(json); });
    },
  },
  created() {
    this.queryData('info',
      (json) => {
        this.info = json;
      });
    this.queryData(this.page,
      (json) => {
        this.details = json[this.page];
      });
  },
};
</script>
